.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
	max-width: 600px;
	width: 100%;
	background-color: #ffffff;
	padding: 20px;
	border-radius: 25px;

	.title {
		text-align: center;
		font-size: 20px;
	}

	label {
		display: flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
		font-size: 17px;
		input {
			border: 1px solid #a3a3a3;
			width: 100%;
			padding: 13px;
			border-radius: 5px;
		}
    span {
      color: red;
      font-size: 16px;
      margin-left: 10px;
    }
	}

	button {
		background-color: #ff7300;
		padding: 13px 0px;
		border-radius: 15px;
		max-width: 250px;
		width: 100%;
		color: #ffff;
	}
}
